  width: 68%;
  height: 100%;
  perspective: 1000px;
  margin-left: 22%;
  position: relative;
  height: 300px;
  margin-top: 2%;
}

.flip__card__inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip__card:hover .flip__card__inner {
  transform: rotateY(180deg);
}

.front__end,
.back__end {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.back__end {
  transform: rotateY(180deg);
}

