/* #########################################Header############################################### */
.dropbtn {
  background-color: #04aa6d00;
  color: white;
  padding: 10px;
  font-size: 16px;
  border: none;
}

.background_div {
  transition: 0.9s;

}

.background_div:hover {
  background-color: #d9bd3e !important;
  color: black;

}

.background_div .hover__heading:hover {
  color: #1E185F !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  z-index: 10000;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 260px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 10000;
  /* margin-top: 5%; */
  border-top: 4px solid #1E185F;
  padding-bottom: 5% !important;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-right: 2px solid #d9bd3e;
  border-bottom: 2px solid #d9bd3e;
  border-left: 2px solid #d9bd3e;
  border-top-right-radius: 7px;
  border-top-left-radius: 7px;

}

.dropdown-content a {
  color: black;
  /* padding: 12px 16px; */
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #dddddd00;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #3e8e4100;
}

.button_style_link {
  background-color: #DBC14A;
  color: white;
}


.mobile__navbar {
  display: none;
}

.video-js .vjs-big-play-button {
  right: 0;
  left: 0;
  width: 4rem;
  bottom: 0;
  height: 3rem;
  top: 10px;
  margin: auto;
  position: absolute;
}

.my-video-dimensions {
  width: 640px;
  height: 570px;
}

.hover__colomn li:nth-child(5) {
  padding-bottom: 16px;
}

.hover__colomn li:nth-child(9) {
  padding-bottom: 16px;
}

.testimonial-slide-video-dimensions {
  /* width: 100%;
    height: 347px; */
}

/* .testimonial-slide-video-dimensions   .vjs-control {
   top: -15px;
} */
#slide-video {
  width: 100%;
  height: 100%;
}

.phone__no {
  margin-bottom: 0px !important;
}

.icons__all {
  /* margin-top: 31%;
    margin-left: 29%; */
}

.short_tab_line_button {
  display: none;
}

.short_tab_ul {
  display: none;
}

.courses__text1:hover+.hover__div {
  border-top: none;
}

.sub__container {
  margin-top: -7px;
}

.hover__div {
  border-radius: 0.5rem;
  /* display: none; */
  position: absolute;
  top: 59px;
  width: 40%;
  right: 0;
  border: 1px solid;
  left: -15px px;
  background: white;
  /* padding: 10px 0px 15px 10px; */
  border-right: 3px solid #d9bd3e;
  z-index: 2;
}

.hover__container {
  padding-bottom: 12px;
}

.hover__container {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-between;
}

.hover__row {
  width: 100%;
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  /* margin-right: -15px; */
  /* margin-left: -15px; */
  box-sizing: border-box;
}

.courses__text {
  cursor: pointer;
  background: initial;
  color: white;
  border: initial;
  outline: initial;
}

.hover__colomn img {
  width: 100%;
  margin-top: 52px;
}

.hover__colomn {
  justify-content: flex-start;
  padding-left: 2%;
  list-style: none;
}

.hover__item {
  margin: 5px 2px;
  display: list-item;
  text-align: -webkit-match-parent;
  border-bottom: 1px solid #ededed6b;

}

